import axios from "axios";
import { ElMessage } from "element-plus";
import router from "../router/index";
import { localGet, localSet } from "../storage/index";

// localSet(
//   "token",
//   "2433b1c003b3b6f62b7a41a703ca0edaaf83feff1fae257ed7f83d480b6496f3"
// );
// 请求路由
var debug = process.env.NODE_ENV === "development";
axios.defaults.baseURL = "";
if (debug) {
  axios.defaults.baseURL = "http://192.168.0.150:9999";
}

// 携带cookie对目前的项目没有什么作用，因为我们是token鉴权
axios.defaults.withCredentials = false;
// 请求头 headers信息
axios.defaults.headers["X-Requested-With"] = "XMLHttpRequest";
axios.defaults.headers.common["Token"] = localGet("token") || "";
// axios.defaults.headers.post["Content-Type"] = "application/json";

axios.interceptors.response.use((res) => {
  // console.log("http result :" + JSON.stringify(res.data));
  if (typeof res.data !== "object") {
    ElMessage.error("服务器异常！");
  }
  if (res.data.code != 0) {
    // if (res.data.message) ElMessage.error(res.data.message);
    if (res.data.code == -99) {
      // router.push({ path: "/login" });

      return Promise.reject(res.data.message);
    }
  }
  return res.data;
});

function httpGet(callback, errback, url, params = {}) {
  axios
    .get(url, { params: params })
    .then(function (response) {
      callback(response);
    })
    .catch(function (err) {
      errback(err);
    });
}

function httpPostParams(callback, errback, url, params = {}) {
  axios
    .post(url, { params: params })
    .then(function (response) {
      callback(response);
    })
    .catch(function (err) {
      errback(err);
    });
}

function httpPostBody(callback, errback, url, params = {}) {
  axios({ url: url, data: params, method: "post" })
    .then(function (response) {
      callback(response);
    })
    .catch(function (err) {
      errback(err);
    });
}

// file
function httpPostData(callback, errback, url, params = {}) {
  axios
    .post(url, params, {
      headers: { "Content-Type": "multipart/form-data" },
    })
    .then(function (response) {
      callback(response);
    })
    .catch(function (err) {
      errback(err);
    });
}

export { httpGet, httpPostData, httpPostParams, httpPostBody };
